.info-page {
  .phones {
    width: 100%;
    background: #fff;
  }

  .content {
    padding-left: 70px;

    &__text {

      font-weight: normal;
      font-size: 14px;
      line-height: 19px;

      color: #606060;


      h1 {
        max-width: 600px;
        margin-bottom: 20px;
      }

      .date {
        font-size: 14px;
        line-height: 19px;
        color: #000000;
        font-weight: 600;
        margin-bottom: 30px;
      }


      p {
        float: left;

      }

      img {
        float: right;
      }

      li {
        color: #000;
      }

      ul {
        padding-left: 5px;

        li {
          position: relative;
          display: flex;
          margin-bottom: 10px;

          &::before {
            content: '';
            display: block;
            width: 6px;
            height: 6px;
            background: black;
            margin-top: 8px;
            margin-right: 20px;
          }
        }
      }

      ol {
        padding-left: 5px;
      }

      ol>li {
        margin: 0;
        list-style-type: none;
        counter-increment: item;
      }

      ol>li:before {
        display: inline-block;
        font-weight: bold;
        text-align: left;
        width: 13px;
        content: counter(item) ".";
        margin-right: 15px;
      }


    }

    &__img {
      float: right;
      margin-left: 75px;
      margin-bottom: 140px;
      margin-top: -390px;
      max-width: 494px;
      max-height: 571px;
      width: 100%;
      height: 100%;
      position: relative;

      &::before {
        content: '';
        position: absolute;
        max-width: 494px;
        max-height: 678px;
        width: 100%;
        height: 100%;
        left: 0;
        top: 100px;
        background: $yellow-green;
        z-index: 1;
      }

      img {
        z-index: 9;
        position: relative;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

  }



}

.default-select {
  appearance: none;
  padding: 5px 10px;
  border: none;
  border-bottom: 1px solid black;
  outline: none;
  padding-left: 0;
  width: 100%;
  background-image: url('../img/select-arrow.svg');
  background-position: right;
  background-repeat: no-repeat;
  cursor: pointer;
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;

  color: #000000;
  margin-bottom: 30px;
}

.question {
  margin: 100px 0px;
    position: relative;
    &::before{
        content: '';
        position: absolute;
        width: 615px;
        height: 609px;
        left: 0;
        bottom: -70px;
        background: $purple-blue;
        z-index: 1;
    }
  .question__inner {
    background-color: #fff;
    padding: 70px;
    display: flex;
    z-index: 2;
    position: relative;
    .question__form,
    .question__text {
      width: 50%;
    }

    .question__text {
      margin-right: 30px;

      h3 {
        margin-top: 0;
      }

      p {
        font-weight: normal;
        font-size: 14px;
        line-height: 19px;
        color: #606060;
        max-width: 400px;
      }
      a{
        transition: opacity 0.2s linear;

        &:hover {
          opacity: 0.7;
        }
      }
      .phones-wrapper {
        display: flex;
        padding-top: 30px;

        .phones {
          font-weight: 500;
          font-size: 14px;
          line-height: 19px;
          color: #575757;
          max-width: 150px;

          ul {
            padding-left: 0;
            list-style: none;

            li a {
              font-size: 18px;
              line-height: 25px;
              color: #000000;
              
            }

            li {
              margin-bottom: 3px;
            }
          }
        }


        .social-phones ul {
          display: flex;
          list-style: none;
          padding-left: 25px;
          margin-top: 37px;

          li {
            margin-right: 15px;
          }
        }
      }

      
      .email{
        display: flex;
        flex-direction: column;
        font-size: 14px;
        line-height: 19px;
        color: #575757;
        a{
            margin-top: 11px;
            color: #000;
        }
    }
    }

    .question__form {
      display: flex;
      flex-direction: column;

      label {
        display: flex;
        flex-direction: column;
        font-weight: normal;
        font-size: 12px;
        line-height: 16px;
        margin-bottom: 30px;
      }

      textarea {
        resize: none;
      }

      select {
        margin-bottom: 40px;
      }

      .default-btn {
        max-width: 145px;
      }
    }
  }
}


.breadcrumbs {
  margin-left: 70px;
  margin-top: 50px;

  ul {
    padding-left: 0;
    list-style: none;
    display: flex;
    font-size: 14px;
    line-height: 19px;

    a {
      color: #000000;
      transition: opacity 0.2s linear;

      &:hover {
        opacity: 0.7;
      }
    }

    li {
      padding-right: 15px;
      margin-right: 30px;
      position: relative;
      display: flex;

      &::after {
        content: '';
        position: absolute;
        height: 2px;
        width: 10px;
        display: block;
        background: #000;
        right: -13px;
        top: 50%;

      }

      &:last-child::after {
        display: none;
      }

      &:last-child {
        color: #575757;
        cursor: default;
      }
    }
  }
}



.house-location{
    margin-top: 150px;
    margin-bottom: -120px;
    .map-wrapper{
        width: 100%;
        padding-right: 70px;
        height: 500px;
        position: relative;
        &::before{
            content: '';
            background: $blue-green;
            width: 71px;
            height: 500px;
            position: absolute;
            right: 0;
        }
        #map{
            width: 100%;
            height: 100%;
        }

        .map-control{
            position: absolute;
            right: auto;
            left: 0;
            &-plus{
                left: 90px;
            }
        }

        
    }
   
}



@media (max-width: 1200px){
  .info-page .content{
    padding-left: 20px;
    padding-right: 20px;
  }

  .breadcrumbs{
    padding-top: 70px;
    margin-left: 20px;
  }

  .question .question__inner .question__text .phones-wrapper .phones{
    display: block !important;
  }
}



@media (max-width: 981px){
  .info-page .content{
    display: flex;
    flex-direction: column;
  }

  .info-page .content__img{
    float: none;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: 0;
    img{
      padding-bottom: 30px;
    }
  }

  .info-page .content__text p{
    float: none;
  }

  .question__inner{
    flex-direction: column;
    p{
      max-width: 100% !important;
    }
    .question__text , .question__form{
      width: 100% !important;
    }

    .question__form{
      margin-top: 50px;
    }
  }
}


@media (max-width: 780px){
  .house-location{
    margin-bottom: 150px;
  }
  .house-location .map-wrapper{
    padding-right: 0;
    &::before{
      width: 200px;
      height: 50px;
      bottom: -50px;
    }
  }
}


@media (max-width:480px){
  .info-page .content__text h1{
    font-size: 36px !important;
    line-height: 49px;
  }

  .question .question__inner{
    padding: 20px !important;
  }

  .phones-wrapper{
    flex-direction: column;
  }

  .question .question__inner .question__text .phones-wrapper .social-phones ul{
    padding-left: 0;
    margin-top: 10px;
  }
}