.popup{
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 99;
    background-color: rgba(0,0,0,0.5);
    top: 0;
    visibility: hidden;
    opacity: 0;
    transition: all 0.2s linear;
    padding: 20px;

    &--show{
        visibility: visible;
        opacity: 1;
        .popup-block{
            transform: scale(1);
        }
    }

    
    &-block{
        max-width: 530px;
        width: 100%;
        padding: 70px;
        padding-left: 140px;
        margin: 130px auto;
        position: relative;
        background-color: #fff;
        transform: scale(0);
        transition: transform 0.2s linear;
        form{
            display: flex;
            flex-direction: column;
            .row{
                display: flex;
                justify-content: space-between;
                flex-direction: row;
                margin-bottom: 30px;
                input , select,{
                    width: 48%;
                    background-image: url('../img/arrow-down.svg');
                    background-position: right;
                    background-repeat: no-repeat;
                    cursor: pointer;
                }

                .timepicker-wrap{
                    width: 48%;
                    input{
                        width: 100%;
                    }
                }

               
            }
            input[type='submit']{
                max-width: 145px;
            }
            label{
                font-weight: normal;
                font-size: 12px;
                line-height: 16px;
                display: flex;
                flex-direction: column;
                margin-bottom: 30px;

                .error-message{
                    text-align: right;
                    color: #DA3838;
                    display: none;
                    margin-top: 10px;
                    &--active{
                        display: block;
                    }
                }
            }
        }
        a[href='#popup-close']{
            position: absolute;
            top: 25px;
            right: 25px;
        }
        h3{
            margin-top: 0;
            padding-top: 0;
            margin-bottom: 40px;
        }
        &::before{
            content:'';
            position: absolute;
            width: 70px;
            height: 100%;
            left: 0;
            top: 0;
            background: $purple-blue;
        }
    }
}
.default-input:disabled{
    color: #575757;
    &::placeholder{
        color: #575757;
    }
    border-bottom: #575757;

}

.default-input{
    border: none;
    outline: none;
    border-bottom: 1px solid black;
    padding: 10px;
    padding-left: 0;
    font-size: 14px;
    line-height: 19px;
    color: #575757;

    &::placeholder{
        color: #575757;
    }

    &--invalid{
        border-color: #DA3838;
    }

    &:focus, &:focus::placeholder{
        color: #000;
    }
}


@media (max-width: 780px){
    .popup-block h3{
        max-width: 215px;
    }
    .popup-block{
        margin-top: 30px;
        overflow: auto;
        padding: 40px;
        a[href='#popup-close']{
            z-index: 5;
        }
        h3{
            font-size: 24px !important;
            position: relative;
            z-index:10;
        }

    }
    .popup-block::before{
        display: none;
    }
    
}

.ymaps-2-1-79-balloon__layout{
    overflow: visible !important;
    .ymaps-2-1-79-balloon__content ymaps{
        overflow: visible !important;
    }

    
}

