.baloon{
    .info-wrap{
        display: flex;
        div > span{
            display: block;
            margin-bottom: 5px;
        }
        .house-img{
            margin-top: -30px;
            margin-right: 10px;
        }
    }

    .house-price{
        display: flex;
        justify-content: space-between;
        font-weight: 600;
        font-size: 20px;
        line-height: 27px;
        color: #000000;
        padding-top: 15px;
        margin-top: 15px;
        margin-bottom: 5px;
        border-top: 1px solid #ECECEC;
    }
}

.popup-house{
    background: none;
    .popup-block{
        position: absolute;
        left: 0;
        top: 0;
        .info-wrap{
            display: flex;
            border-bottom: 1px solid #ECECEC;
            margin-bottom: 10px;
            padding-bottom: 25px;
            font-size: 14px;
            span{
                display: block;
                margin-bottom: 20px;
                
            }
        }
        &::before{
            display: none;
        }
        padding: 15px;
        max-width: 280px;
        svg{
            width: 16px;
            height: 16px;
            
        }

        .house-img{
            margin-top: -30px;
            margin-right: 20px;
        }

        .house-price{
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-weight: bold;
            svg{
                width: 30px;
            }
        }

        a[href="#popup-close"]{
            top: 10px;
            right: 10px;
        }
    }
    
}



.timepicker-wrap{
    position: relative;
    
    .timepicker-list{
        position: absolute;
        top: 50px;
        left: 0;
        width: 204px;
        background: #FFFFFF;
        border: 1px solid #ECECEC;
        box-sizing: border-box;
        padding: 10px 20px;
        opacity: 0;
        visibility: hidden;
        transition: opacity 0.2s linear, visibility 0.2s linear;
        &--active{
            visibility: visible;
            opacity: 1;
        }

        ul{
            list-style: none;
            padding-left: 0;
            max-height: 180px;
            overflow: auto;

              /* width */
        &::-webkit-scrollbar {
            width: 2px;
        }
        
        /* Track */
        &::-webkit-scrollbar-track {
            background: #ECECEC;
        }
        
        /* Handle */
        &::-webkit-scrollbar-thumb {
            background: rgb(0, 0, 0);
        }
        
        /* Handle on hover */
        &::-webkit-scrollbar-thumb:hover {
            background: #555;
        }


        }
        li{
            font-size: 14px;
            margin-bottom: 11px;
            transition: background-color 0.2s linear;
            padding: 2px;
            margin-right: 20px;
            cursor: pointer;
            &:hover{
                background-color: #000;
                color: #fff;
            }
        }

    
    }

      
}

@media(max-width: 780px){
    .timepicker-wrap{
        position: static;
    }

   
    .timepicker-wrap .timepicker-list{
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 10;
       
    }
}