.cottege-page {
  .cottege {
    &-information__inner {
      display: flex;

      .main-info {
        max-width: 60%;
        width: 100%;
        padding: 60px;
        background-color: #fff;
        margin: 40px;
        margin-left: 0;
        position: relative;
        z-index: 2;

        a.back {
          display: flex;
          align-items: center;
          color: black;

          svg {
            margin-right: 10px;
          }
        }

        h1 {
          display: flex;
          align-items: center;
        }

        .cottege-status {
          font-size: 14px;
          line-height: 19px;
          color: #FFFFFF;
          background: url('../img/status-bg.webp');
          padding: 7px 20px;
          background-repeat: no-repeat;
          background-color: #fff;
          background-size: cover;
          background-position: center;
          margin-left: 20px;
          margin-top: 7px;
        }

        .short-descr {
          font-weight: normal;
          font-size: 20px;
          line-height: 27px;
          color: #000000;
          max-width: 550px;
          border-left: 4px solid black;
          padding-left: 30px;
          margin-bottom: 40px;
        }

        .descr {
          max-width: 800px;

          p {
            overflow: hidden;
            max-height: 223px;
            position: relative;

            &::before {
              content: '';
              position: absolute;
              width: 100%;
              height: 100%;
              bottom: 0;
              left: 0;
              background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%);
            }

            transition: max-height 1s linear;

          }

          p.opened {
            &::before {
              display: none;
            }

            transition: max-height 1s linear;

            height: auto;
            max-height: 1000px;
          }

          .show-more {
            color: black;
            display: flex;
            align-items: center;
            font-size: 14px;
            margin-top: 30px;

            svg {
              margin-right: 10px;
              transition: all 0.2s linear;
            }
          }

          .is-active {
            svg {
              transform: rotate(-180deg);
            }
          }
        }



      }


      .readiness {
        padding-top: 30px;

        p {
          font-weight: normal;
          font-size: 14px;
          line-height: 19px;
          color: #606060;
          max-width: 360px;
        }

        &-dates {
          display: flex;
          align-items: center;
          margin-top: 30px;

          .finish-date {
            margin-left: 45px;

            span {
              display: block;

              &:first-child {
                margin-bottom: 5px;
              }
            }
          }
        }

        .price {
          font-size: 46px;
          line-height: 63px;
          color: #000000;
          display: block;
          margin-top: 25px;
          margin-bottom: 35px;
        }
      }
    }



  }


  .layout {
    max-width: 560px;
    margin-left: auto;
    padding: 70px;
    padding-left: 160px;
    background: $purple-blue;
    margin-top: -250px;
    position: relative;

    .block-title {
      position: absolute;
      left: -900px;
      top: -100px;
      z-index: 1;
    }

    ul {
      font-weight: normal;
      font-size: 14px;
      line-height: 19px;
      padding-left: 0;
      /* black */

      color: #000000;

      list-style: none;
      padding-left: 3px;

      li {
        position: relative;
        display: flex;
        margin-bottom: 20px;

        &::before {
          content: '';
          display: block;
          width: 6px;
          height: 6px;
          background: black;
          margin-top: 8px;
          margin-right: 20px;
        }
      }
    }
  }



  .benefits {
    margin-top: -150px;
    margin-bottom: 150px;
    .benefits-inner{
      margin-bottom: -2px;
      margin-right: -2px;
      overflow: hidden;
    }
    h2 {
      text-align: left;
      max-width: 350px;
      margin-bottom: 80px;
    }

    .benefits-grid {
      display: grid;
      grid-template-rows: auto;
      grid-template-columns: repeat(3, 1fr);
      overflow: hidden;
      margin-bottom: -1px;
      margin-right: -1px;
    }

    .benefits-item {
      display: flex;
      align-items: center;
      border-bottom: 1px solid #DEDEDE;
      padding: 50px 0px;
      padding-right: 20px;
      padding-left: 20px;
      border-right: 1px solid #DEDEDE;

      img {
        margin-right: 30px;
        font-size: 14px;
        line-height: 19px;
        color: #000000;
      }
    }
  }


  .map{
      position: relative;
      &::before{
          content: '';
          background: $yellow-green;
          position: absolute;
          width: 250px;
        height: 710px;
        left: 0;
        bottom: -120px;
      }
      .block-title{
          left: auto;
          right: -300px;
          width: auto;

      }
  }
}





// readiness progress bar

.progress-bar-wrapper {
  display: flex;
  align-items: center;
  margin-top: 5px;

  .percent {
    font-weight: bold;
    margin-left: 5px;
  }
}

.progress-bar {
  width: 120px;
  height: 10px;
  background-color: #E4E4E4;

  .progress-line {
    height: 10px;
    display: block;
  }


}


.blue-green .progress-line {
  background: $blue-green;
}

.green-yellow .progress-line {
  background: $yellow-green;
}

.purple-red .progress-line {
  background: linear-gradient(134.62deg, #E2D9F1 0%, #E79196 99.34%);
  ;
}



@media(max-width: 1080px){
  .cottege-page .cottege-information__inner{
    flex-direction: column;
    .main-info{
      width: 100%;
      max-width: 100%;
    margin: 0 !important;
    padding: 30px 20px;

    padding-top: 80px;
    }

    .readiness{
      padding: 20px;
    }
  }

  .cottege-information .container{
    padding: 0px;
  }

  .cottege-page .layout{
    margin-top: 20px;
    margin-left: 0;
    width: 100%;
    max-width: 100%;
    padding: 20px;
  }

  .layout-container{
    padding: 0 !important;
  }

  .cottege-page .benefits{
    margin-top: 0;
    margin-bottom: 50px;
    h2{
      margin-bottom: 0px !important;
    }
  }

  

  .cottege-page .map{
    padding-top: 0;
  }
}

.cottege-pagination {
  display: none;
  padding-top: 10px;
  margin-top: 0 !important;
  background: #fff;
  justify-content: center;
}


@media (max-width: 780px){

  .cottege-prev , .cottege-next{
    display: none !important;
  }
  .cottege-btns{
    bottom: 15px !important;
  }
  .cottege-pagination{
    display: flex !important;

    z-index: 15;
  }
  .cottege-name{
    font-size: 36px;
  }

  .short-descr p{
    font-size: 18px;
  }
  .benefits-grid{
    grid-template-columns: repeat(2,1fr) !important;
  }

  .benefits-item{
    flex-direction: column;
    align-items: flex-start !important;
    font-size: 14px;
    img{
      margin-bottom: 30px;
    }
  }

  .descr p{
    font-size: 14px;
  }

  .benefits-inner h2{
    line-height: 41px;
  }
}


@media (max-width: 480px){
  .cottege-page .main-screen .cottege-btns{
    top: 68px;
    bottom: auto !important;
    width: 100%;
    .default-btn{
      width: 50%;
    }
  }

  .cottege-page .main-screen{
    padding-top: 150px;
  }
}