.home-page {
  .main-screen {
    background-image: url('../img/main-screen-bg.png');
    background-position: right;
    background-repeat: no-repeat;
    background-size: 680px;
    background-color: #fff;

    .phones {
      max-width: 550px;
      background: #fff;
      position: relative;
      z-index: 15;
    }

    &__content {
      position: relative;

      &::after {
        position: absolute;
        content: '';
        width: 220px;
        height: 230px;
        background-image: url('../img/leaf.png');
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        right: 239px;
        top: -84px;
        opacity: 0;
        animation-name: leaf;
        animation-delay: 2s;
        animation-duration: 1000s;

      }


      &-wrapper {
        display: flex;
        justify-content: space-between;
        overflow: visible;
        width: 100%;
        max-height: 590px;
        position: relative;
       

        &::before{
          transition: width 0.2s linear;
          width: 0%;
          height: 100%;
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          background: $purple-blue;
          animation-name: main-bg;
          animation-delay: 1s;
          animation-duration: 1000s;
        }
        
        background-size: 65%;
        background-repeat: no-repeat;
      }


      &-img {
        z-index: 9;
        position: absolute;
        right: 0;
        overflow: visible;
        bottom: -4px;
        width: 45vw;
        img {
          width: 100%;
        }
      }

      padding-top: 80px;
      padding-bottom: 80px;

      z-index: 1;

      p,
      h1,
      h2,
      h3 {
        max-width: 300px;
        margin: 0;
      }

      h1 {
        position: relative;

        &::after {
          position: absolute;
          content: '';
          width: 28px;
          height: 38px;
          background-image: url('../img/small-leaf.png');
          background-size: cover;
          background-position: center;
          background-repeat: no-repeat;
          right: 161px;
          top: -45px;
          animation-name: left;
          animation-delay: 2;
          animation-duration: 2s;

          opacity: 0;
          animation-name: leaf;
          animation-delay: 2s;
          animation-duration: 1000s;
        }

      }

      p {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 19px;

        color: #606060;
        padding-top: 50px;
        padding-bottom: 30px;
        margin: 0;
      }
    }
  }

  .about-text {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      width: 26.042vw;
      height: 0vw;
      background: $yellow-green;
      top: -20px;
      right: 0px;
      z-index: 5;

      animation-name: main-bg2;
      animation-delay: 1.3s;
      animation-duration: 1000s;
    }

    &::after {
      position: absolute;
      content: '';
      width: 220px;
      height: 150px;
      background-image: url('../img/leaf2.png');
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      right: 100px;
      top: -80px;
      z-index: 15;

      opacity: 0;
      animation-name: leaf;
      animation-delay: 2s;
      animation-duration: 1000s;
    }


    p {
      max-width: 550px;
      font-style: normal;
      font-weight: normal;
      font-size: 20px;
      line-height: 27px;

      color: #000000;
      padding-top: 130px;
      padding-bottom: 50px;
    }

    .block-title {
      position: absolute;
      top: -45px;
      right: -200px;
      z-index: 1;
    }
  }


  .products-list {
    max-width: 1087px;
    width: 100%;
    background-color: $white;
    margin: 0 auto;
    margin-bottom: 80px;
    position: relative;
    z-index: 5;

    &--grid {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: auto;

    }

    .reserve .product-item-img {
      position: relative;

      &::after {
        content: 'Резерв';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        padding: 10px 30px;
        text-align: center;
        background: linear-gradient(180deg, #E7886A 0%, #DA4848 100%);
        line-height: 19px;
        text-transform: uppercase;
        color: #FFFFFF;

      }

    }

    .soon {
      display: flex;
      justify-content: center;
      align-items: center;
      background-image: url('../img/cross.svg');
      background-position: center;
      background-size: cover;

      div {
        text-transform: uppercase;
        color: #fff;
        background: #CCCCCC;
        padding: 15px 30px;
        max-width: 145px;
        text-align: center;
      }
    }

    .product-item {
      padding: 40px 50px;
      border: 1px solid #f6f6f6;
      min-height: 712px;
      margin: -1px;
      box-sizing: border-box;
      border-collapse: collapse;
      border-top: 0;

      span {
        display: block;
      }

      .product-location {
        color: #575757;
        padding-top: 25px;
        padding-bottom: 10px;
      }

      .product-name {
        font-weight: normal;
        font-size: 20px;
        line-height: 27px;
        color: #000000;
      }

      .product-descr {
        color: $gray;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 19px;
        min-height: 68px;
      }

      .product-readiness {
        font-size: 14px;
        color: $gray;
        margin-top: 40px;
        margin-bottom: 15px;

        .progress-bar-wrapper {
          display: flex;
          align-items: center;
          margin-top: 5px;

          .percent {
            font-weight: bold;
            margin-left: 5px;
          }
        }

        .progress-bar {
          width: 120px;
          height: 10px;
          background-color: #E4E4E4;

          .progress-line {
            height: 10px;

          }


        }

        .blue-green .progress-line {
          background: $blue-green;
        }

        .green-yellow .progress-line {
          background: $yellow-green;
        }

        .purple-red .progress-line {
          background: linear-gradient(134.62deg, #E2D9F1 0%, #E79196 99.34%);
          ;
        }
      }


      .be-ready {
        font-size: 14px;
        color: $gray;

        b {

          color: $black;
        }
      }

      .price {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-style: normal;
        font-weight: normal;
        font-size: 30px;
        line-height: 41px;
        margin-top: 25px;

        .product-btn {
          width: 50px;
          height: 50px;
          padding: 0;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }


      &-img {
        width: 261px;
        height: 261px;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
  }


  .benefits .container {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }

  .benefits {
    position: relative;
    padding-bottom: 60px;

    &::after {
      width: 22.500vw;
      height: 45.833vw;
      background: $purple;
      content: '';
      position: absolute;
      top: -800px;
      left: 0;
      z-index: 1;
    }

    .block-title {
      position: absolute;
      top: 0px;
      z-index: 1;
      left: -320px;
    }

    &-text {
      margin-right: 80px;
      max-width: 325px;

      h2 {
        text-align: left;
      }

      p {
        font-weight: normal;
        font-size: 20px;
        line-height: 27px;

        color: #000000;
      }
    }

    &-list {
      z-index: 10;
      display: grid;
      grid-template-rows: auto;
      grid-template-columns: repeat(2, 1fr);
      grid-column-gap: 140px;
      grid-row-gap: 70px;
    }

    &-item {

      p {
        max-width: 200px;
      }
    }

    &-title {
      max-width: 200px;
      font-weight: 600;
      margin-top: 20px;
      margin-bottom: 10px;
      display: block;
    }


  }




  .information {
    padding: 70px;

    position: relative;
    padding-top: 150px;

    &::before {
      width: 20.833vw;
      height: 62.500vw;
      background: $blue-green;
      content: '';
      position: absolute;
      right: 0;
      top: -648px;
      z-index: 0;
    }

    .container {
      display: flex;
      justify-content: space-between;
      position: relative;
    }

    &-text {
      &--title {
        text-align: left;
        max-width: 405px;
        margin-bottom: 70px;
      }

      p {
        color: #575757;
        max-width: 370px;
      }

      .size {
        font-style: normal;
        font-weight: 600;
        font-size: 50px;
        line-height: 68px;
        color: #000000;
        display: block;
        padding-bottom: 10px;
      }
    }

    &-pagination {
      margin-top: 50px;

      .swiper-pagination-bullet {
        opacity: 1;
        background-color: black;
        width: 6px;
        height: 6px;
      }

      .swiper-pagination-bullet-active {
        opacity: 1;
        background-color: #D7D7D7;
      }
    }
  }





}





.map {
  position: relative;
  padding-top: 140px;
  margin-top: 100px;

  &-wrapper {
    display: flex;
    align-items: flex-end;
    position: relative;
    justify-content: flex-end;
  }

  &-control {
    z-index: 5;
    background-color: black;
    transition: background-color 0.2s linear;
    width: 90px;
    height: 90px;
    bordeR: none;
    outline: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover {
      background-color: $gray;
    }
  }

  .location {
    max-width: 540px;
    width: 100%;
    height: 430px;
    z-index: 5;
    padding: 90px 60px;
    background-color: #fff;

    select {
      appearance: none;
      padding: 5px 10px;
      border: none;
      border-bottom: 1px solid black;
      outline: none;
      padding-left: 0;
      width: 100%;
      background-image: url('../img/select-arrow.svg');
      background-position: right;
      background-repeat: no-repeat;
      cursor: pointer;
      font-weight: 500;
      font-size: 14px;
      line-height: 19px;
      background-color: #fff;
      color: #000000;
      margin-bottom: 30px;

    }


    p {
      font-size: 14px;
      line-height: 19px;

      color: #575757;
      max-width: 320px;
    }

    h2 {
      text-align: left;
    }

    a {
      display: flex;
      align-items: center;
      font-weight: 600;
      font-size: 14px;
      line-height: 19px;

      color: #000000;

      svg {
        margin-right: 5px;
      }
    }
  }

  .block-title {
    position: absolute;
    top: -245px;
    z-index: 1;
    width: 3000px;
  }

  #map {
    z-index: 5;
    position: absolute;
    height: 500px;
    width: 80%;
    left: 0;
  }


}


.articles {
  margin-top: 90px;
  margin-bottom: 190px;
  position: relative;

  .block-title {
    position: absolute;
    bottom: -450px;
    width: 3000px;
    left: -800px;
  }

  &::before {
    content: '';
    position: absolute;
    width: 40.104vw;
    height: 20.052vw;
    background: linear-gradient(134.62deg, #ABABAB 0%, #ECE4CF 79.68%);
    left: 0;
    bottom: -70px;
  }

  h2 {
    text-align: left;
  }

  .articles-prev,
  .articles-next {
    cursor: pointer;
    position: absolute;
    right: 0;
    top: -15px;
    width: 90px;
    height: 90px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.2s linear;

    svg path {
      transition: stroke 0.2s linear;
    }

    &:hover {
      background-color: black;
    }

    &:hover path {
      stroke: white;
    }
  }

  .articles-prev {
    right: 90px;
  }

  &__inner {
    width: 100%;
    overflow: hidden;

  }

  .container {
    position: relative;

  }

  &-slider {
    width: 100%;
    display: flex;
    height: 315px;

  }

  &-slide {
    flex-shrink: 0;
    height: 315px;
    overflow: hidden;
    display: block;
    position: relative;
    display: flex;
    align-items: flex-end;

    &::before {
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: 3;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
      opacity: 0.5;
    }

    img {
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: transform 0.2s linear;
      z-index: 1;
      top: 0;
      left: 0;
    }

    .articles-name {
      z-index: 5;
      font-weight: 500;
      font-size: 14px;
      line-height: 19px;
      color: #FFFFFF;
      padding: 20px;

    }

    &:hover {
      img {
        transform: scale(1.1);
      }
    }

  }
}


@media (max-width: 1200px) {
  .footer {
    padding: 0px 20px !important;
  }

  .information .container {
    flex-direction: column;
  }

  .information-text {
    margin-bottom: 50px;
  }
}


@media (max-width: 1080px) {

  .footer {
    flex-direction: column;
    height: auto !important;

    .copyright {
      order: 3;
      padding: 20px 0px;
    }

    .footer-menu {
      order: 1;
      padding: 20px 0px;
      border-bottom: 1px solid #363232;
      flex-wrap: wrap;

      li {
        margin-bottom: 15px;
      }
    }

    .footer-phone {
      padding: 20px 0px;
      order: 2;
      border-bottom: 1px solid #363232;

    }
  }
  .map{
    margin-bottom: 150px;
  }
  .map-wrapper {
    justify-content: center;
    height: 700px;

    .location {
      margin: 20px;
      margin-bottom: -100px;
    }

    #map {
      width: 100%;
      height: 700px;
      bottom: 20px;
    }

    .map-control {
      display: none;
    }
  }

  .block-title {
    display: none;
  }

  .home-page {
    .main-screen {
      &__content {
        padding-left: 20px;
        padding-top: 100px;
      }
    }
  }

  .home-page .about-text .block-title {
    right: -500px;
  }

  .home-page .products-list--grid {
    grid-template-columns: repeat(2, 360px);
    justify-content: center;
    max-width: 720px;
    margin: 0 auto;
  }

  .home-page .benefits {
    margin-bottom: 50px;

    .block-title {
      display: none;
    }

    .container {
      flex-direction: column;
      align-items: start;
      justify-content: flex-start;
      padding-top: 80px;
    }

    &-text {
      max-width: 100%;
      margin-bottom: 30px;
    }

    .benefits-list {
      &-wrapper {
        width: 100%;
      }

      grid-template-columns: repeat(3, 1fr);
      grid-column-gap: 70px;
    }
  }
}

.home-list .swiper-pagination {
  display: none;
}

@media (max-width: 780px) {
  .home-page .main-screen__content-wrapper{
    background-size: 100%;
  }
  .swiper-pagination-bullet {
    opacity: 1;
    background-color: black;
    width: 6px;
    height: 6px;
  }

  .swiper-pagination-bullet-active {
    opacity: 1;
    background-color: #D7D7D7;
  }

  .size {
    font-size: 36px !important;
    margin-top: -15px;
  }

  .home-page .information {
    padding: 20px 0px;
    margin-top: 50px;

    .information-text--title {
      margin-bottom: 0;
    }
  }


  h2 {
    font-size: 30px !important;
  }

  .home-page .benefits-text p {
    font-size: 18px;
  }

  .main-screen__content {
    width: 100% !important;
  }

  .main-screen__content-wrapper {
    max-height: 755px !important;
    height: 755px;
  }

  .home-page .main-screen__content-img {
    width: 340px;
  }

  .home-page .about-text::before {
    width: 320px;
    height: 90px;
  }

  .home-page .about-text p {
    padding-left: 20px;
    max-width: 100%;
    width: 100%;
    padding-right: 20px;
  }


  .home-page .products-list--grid {
    grid-template-columns: repeat(1, 450px) !important;
    max-width: 450px;

  }

  .home-page .products-list .product-item-img {
    width: 100%;
  }

  .home-list {
    height: auto !important;

    .home-list-slider {
      height: auto !important;
    }

    .swiper-pagination {
      display: block;
      bottom: -25px;
    }

    .home-list-prev,
    .home-list-next {
      display: none;
    }
  }

  .home-page .information::before {
    width: 90px;
    height: 800px;
    top: -380px;
  }




  .home-page .products-list--grid-v2 {
    grid-template-columns: repeat(2, 1fr) !important;
    max-width: 100%;

    .product-item-img {
      height: auto;

      &::after {
        transform: translate(0) !important;
        top: 0 !important;
        left: 0 !important;
      }
    }

    .product-item {
      padding: 20px !important;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      max-height: 420px;

      .product-descr {
        display: none !important;
      }

      .price {
        flex-direction: column !important;
        align-items: flex-start;
        font-size: 20px;

        .product-btn {
          margin-top: 20px;
        }
      }
    }

    .soon {
      justify-content: center;
    }


  }
}



@media (max-width: 480px) {
  .home-page .main-screen__content-wrapper::before{
    width: 350px !important;
  }
  .footer {
    align-items: flex-start !important;
  }

  .articles__inner,
  .articles-slider {
    width: 270px;
    overflow: visible;
  }

  .articles__inner {
    margin-left: -20px;
  }

  .information-slider__wrapper {

    .information-slider-prev,
    .information-slider-next {
      display: none;
    }
  }

  .information-slider__wrapper .information-slider {
    height: 250px !important;
  }

  .home-page .products-list--grid {
    grid-template-columns: repeat(1, 1fr) !important;
    max-width: 100%;

  }



  .map {
    padding-top: 20px;
  }

  .home-page .products-list .product-item-img {
    width: 100%;
    height: 320px;
  }

  .home-page .products-list .product-item {
    padding: 20px;
  }

  .benefits-list-wrapper {
    width: 200px !important;
  }

  .home-page .benefits-list {
    display: flex;
    width: 200px;
    grid-column-gap: 0px !important;
    grid-template-rows: repeat(1, 1fr);
  }

  .benefits-item {
    flex-shrink: 0;
    padding-right: 15px;

    p {
      font-size: 14px;
    }

    span {
      font-size: 14px;
    }
  }

  .home-page .products-list--grid-v2 {
    grid-template-columns: repeat(2, 1fr) !important;
    max-width: 100%;

    .product-item-img {
      height: auto !important;
    }
  }
}



@keyframes main-bg {
  0%{
    width: 0%;
  }
  .2%{
    width: 65%;
  }
  100%{
    width: 65%;
  }
}


@keyframes main-bg {
  0%{
    width: 0%;
  }
  .2%{
    width: 65%;
  }
  100%{
    width: 65%;
  }
}
@keyframes main-bg2 {
  0%{
    height: 0%;
  }
  .2%{
    height: 7.292vw;
  }
  100%{
    height: 7.292vw;
  }
 
}




@keyframes leaf {
  0%{
    opacity: 0;
    transform: translateY(-30px);
  }
  .2%{
    opacity: 1;
    transform: translateY(0);
  }
  100%{
    opacity: 1;
    transform: translateY(0);
  }

 
 
}

