.footer{
    background-color: $black;
    width: 100%;
    height: 127px;
    margin-top: 120px;
    z-index: 10;
    position: relative;
    color: #979797;
    a{
        color: #979797;
        font-size: 14px;
    }

    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0px 70px;
    font-size: 14px;
    &-menu{
        display: flex;
        list-style: none;
        li{
            margin-right: 40px;
            &:last-child{
                margin-right: 0;
            }
        }
        li a{
            transition: color 0.2s linear;
            &:hover{
                color: #bdbdbd;
            }
        }
    }

    &-phone{
        color: #fff !important;
        font-size: 24px !important;
        transition: opacity 0.2s linear;
        &:hover{
            opacity: 0.8;
        }

    }
}