.header{
    max-width: 140px;
    padding: 30px;
    background-color: #fff;
    z-index: 95;
    position: relative;
    &--fixed{
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        z-index: 95;
    }

    &-menu{
        height: 100vh;
        overflow: hidden;
        padding: 20px;
        width: 350px;
        background: #fff;
        justify-content: center;
        align-items: center;
        position: fixed;
        z-index: 90;
        top: 0;
        display: flex;
        flex-direction: column;
        transition: all 0.2s linear;
        left: -500px;
        &--open{

            left: 139px;
        }
        a{
            color: black;
            font-size: 30px;
           
        }
        ul{
            list-style: none;
            margin-left: 0;
            padding-left: 0;
            li{
                margin-bottom: 40px;
                a{
                    transition: opacity 0.2s linear;
                    &:hover{
                        opacity: 0.5;
                    }
                }
            }
            border-bottom: 1px solid #D7D7D7;
        }

        .phone-wrapper{
            margin-top: 40px;
            a{
                display: flex;
                align-items: center;
                font-size: 24px;
                font-weight: 500;
                margin-bottom: 10px;
                svg{
                    margin-right: 8px;
                }
                &:not(:first-child){
                    margin-left: 32px;
                }
            }

            .callback{
                font-size: 14px;
                margin-left: 0 !important;
                padding-top: 10px;
                svg{
                    width: 15px;
                }
            }
        }
    }
}

.xl-hidden{
    display: none;
}


.menu-phones{
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
    width: 100%;
    min-height: 100vh;
    height: auto;
    overflow: auto;
    z-index: 99;
    padding-top: 100px;
    left: 100%;
    transition: all 0.2s linear;
    a[href='#phones__menu-close']{
        position: absolute;
        right: 20px;
        top: 20px;
    }
    &--active{
        left: 0;
    }
    .phone-wrapper{
        display: flex;
        flex-direction: column;
        align-items: center;
        a{
            font-weight: 500;
            font-size: 24px;
            line-height: 33px;
            color: #000000;
            margin-bottom: 14px;
            &:first-child{
                margin-left: -25px;
                svg{
                    width: 20px;
                    height: 20px;
                }
            } 
        }

        .callback{
            font-size: 14px;
            svg{
                width: 15px;
                height: 10px;
                margin-right: 10px;
            }
        }
    }
}


@media (max-width: 1200px){

    .header {

        &-menu{
            .phone-wrapper{
                display: none;
            }
        }
        .svg-hover{
            svg path{
                opacity: 1;
            }
        }
    }
    .sm-hidden{
        display: none;
    }
    .xl-hidden{
        display: block;
        width: 65px;
    }
    .phones{
        display: none !important;
    }

    .header{
        width: 100% !important;
        right: 0;
        top: 0;
        height: 70px !important;
        display: flex;
        flex-direction: row;
        min-height: 0 !important;
        max-width: 100% !important;
        padding: 10px;

        .header-logo{
            order: 2;
            width: 46.77px;
            height: 48px;
        }

        .xl-hidden{
            order: 1;
        }

        .hamburger{
            order: 3;
        }

        &-menu{
            min-height: 100vh;
            height: auto;
            text-align: center;
            overflow: auto;
            padding-top: 70px;
        }

        
    }

    .header-menu--open{
        width: 100% !important;
        left: 0 !important;
    }

    .site-container {
        margin-left: 0px !important;
    }
    
}