// базовые подключения
@import "vars";
@import "mixins";
@import "fonts";
@import "settings";
@import "home";
@import "sliders";
@import "popups";
@import "cottage-card";
@import "info";

// подключения компонентов страницы
@import "./components/header";
@import "./components/footer";
@import "./components/hamburger";
@import "./components/datapicker";

.page__body{
  opacity: 0;
  transition: opacity 2s linear;
}


.hamburger-inner, .hamburger-inner::before, .hamburger-inner::after{
  height: 2px;
  width: 35px;
}
.hamburger-box{
  width: 35px;
}

.page__body--visible{
  opacity: 1;
}

body, html{
  font-family: 'Manrope', sans-serif;
  font-size: 24px;
}

h1{
  font-size: 50px;
  font-style: normal;
  font-weight: 300;
  font-size: 50px;
  line-height: 68px;
  color: #000000;

}

h2{
  font-size: 46px;
  font-style: normal;
  font-weight: 300;
  font-size: 46px;
  line-height: 63px;
  text-align: center;
  color: #000000;
}

h3{
  font-style: normal;
font-weight: normal;
font-size: 24px;
line-height: 33px;
/* identical to box height */


color: #000000;
}


.site-container{
  margin-left: 140px;
}


path{
  transition: opacity 0.2s linear;

}


.svg-hover{
  transition: opacity 0.2s linear;
  &:hover{
    opacity: 0.5;
  }
}

a>svg path{
  opacity: 0.5;
}

a:hover svg path{
  opacity: 1;
}

.default-btn{
  appearance: none;
  padding: 16px 30px;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;
  text-transform: uppercase;
  color: $white;
  border: none;
  background-color: $black;
  cursor: pointer;
  transition: background 0.2s linear;
  &:hover{
    background: $gray;
  }
}

nav.phones{
  display: flex;
  align-items: center;
  height: 80px;
  padding-left: 70px;
  div, a{
    display: flex;
    align-items: center;
    color: #000;
    font-size: 14px;
    font-weight: 500;

  }

  .phone{
    font-size: 24px;
    margin-right: 7px;
  }

  svg{
    margin-right: 10px;
  }

  .phone-wrap{
    margin-right: 30px;
  }
}

.page__body{
  background-color: #f6f6f6;

}


select{
  background-color: #fff !important;
}

.block-title{
  font-family: 'Lora', serif;
  font-style: normal;
  font-weight: normal;
  font-size: 400px;
  line-height: 512px;
  color: #EEEEEE;

}

.container{
  max-width: 1117px;
  width: 100%;
  margin: 0 auto;
  padding: 0px 15px;
}

