
 path{
    transition: stroke 0.2s linear;
}
.home-list{
    max-width: 1019px;
    width: 100%;
    position: relative;
    height: 570px;
    z-index: 5;
    &-slider{
        display: flex;
        width: 100%;
        height: 570px;

    }

    &-slide{
        flex-shrink: 0;
        z-index: 1;
        height: 100%;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &-prev, &-next{
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 10;
        width: 90px;
        height: 90px;
        background: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        transition: all 0.2s linear;

       

        &:hover{
            background: #000;
        }

        &:hover path{
            stroke: #Fff;
        }
    }

    &-next{
        left: 90px;

    }


    // &-prev.swiper-button-disabled, &-next.swiper-button-disabled{
    //     background: #F6F6F6;
    // }
}

.information-slider__wrapper{
    max-width: 545px;
    overflow: hidden;
    position: relative;
    height: 402px;
    .information-slider{
        display: flex;
        width: 100%;
        
        height: 402px;

        &-next{
            left: 90px!important;
            right: auto;
        }
        &-prev{
            left: 0px !important;
            right: auto;
        }
    }
    .information-slide{
        flex-shrink: 0;
        z-index: 1;
        height: 100%;
        img{

            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}
.slider-prev-arrow, .slider-next-arrow{
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 10;
    width: 90px;
    height: 90px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: all 0.2s linear;


    &:hover{
        background: #000;
    }
    &:hover path{
        stroke: #Fff;
    }
}

.slider-prev-arrow{
    right: 90px;
}

