.cottege-page .main-screen{
    width: 100%;
    height: 100%;
    overflow: hidden;
    height: auto;
    position: relative;
    .cottege-btns{
        display: flex;
        align-items: center;
        position: absolute;
        right: 0;bottom: 0px;
        a, .cottege-prev, .cottege-next{
            width: 90px;
            height: 90px;
            display: flex;
            justify-content: center;
            align-items: center;
            background: #fff;
            transition: all 0.2s linear;
            cursor: pointer;
            svg path{
                opacity: 1;
                stroke: black;
            }
            &:hover{
                background: #000;
            }
            &:hover path{
                stroke: #Fff;
            }

           
        }

       
    }
    .cottege-slider{
        display: flex;
        width: 100%;
        height: 100%;
        
        .cottege-slide{
            flex-shrink: 0;
            img{
                width: 100%;
                height: auto;
                object-fit: cover;
                max-height: 820px;
            }
        }
    }
}



.d-pagination{
    margin-top: 50px;

    .swiper-pagination-bullet {
      opacity: 1;
      background-color: black;
      width: 6px;
      height: 6px;
    }

    .swiper-pagination-bullet-active {
      opacity: 1;
      background-color: #D7D7D7;
    }
}